<template>
  <section class="demo-icon">
    <demo-block :title="t('demo')" :card="true">
      <ul class="fs-row">
        <!-- color -->
        <li
          class="fs-col fs-col-6"
          @click="copy(demoIcon, { color: '#1989fa' })"
        >
          <gw-icon :name="demoIcon" color="#1989fa"/>
          <span>自定义color</span>
        </li>
        <!-- size -->
        <li
          class="fs-col fs-col-6"
          @click="copy(demoIcon, { size: '12' })"
        >
          <gw-icon class="margin-top-36" :name="demoIcon" size="12"/>
          <span>自定义size</span>
        </li>
        <!-- dot -->
        <li
          class="fs-col fs-col-6"
          @click="copy(demoIcon, { dot: true })"
        >
          <gw-icon :name="demoIcon" dot/>
          <span>自定义dot</span>
        </li>
        <!-- badge 9 -->
        <li
          class="fs-col fs-col-6"
          @click="copy(demoIcon, { badge: '9' })"
        >
          <gw-icon :name="demoIcon" badge="9"/>
          <span>自定义badge</span>
        </li>

        <!-- badge 99+ -->
        <li
          class="fs-col fs-col-6"
          @click="copy(demoIcon, { badge: '99+' })"
        >
          <gw-icon :name="demoIcon" badge="99+"/>
          <span>自定义badge+</span>
        </li>

        <!-- url 外链 -->
        <li
          class="fs-col fs-col-6"
          @click="copy(demoImage)"
        >
          <gw-icon :name="demoImage"/>
          <span>外链url</span>
        </li>
      </ul>
    </demo-block>

    <demo-block :title="t('basic')" :card="true">
      <ul class="fs-row">
        <li
          class="fs-col fs-col-6"
          v-for="icon in config.basic"
          :key="icon"
          @click="copy(icon)"
        >
          <gw-icon :name="icon" />
          <span>{{icon}}</span>
        </li>
      </ul>
    </demo-block>

    <demo-block :title="t('outline')" :card="true">
      <ul class="fs-row">
        <li
          class="fs-col fs-col-6"
          v-for="icon in config.outline"
          :key="icon"
          @click="copy(icon)"
        >
          <gw-icon :name="icon" />
          <span>{{icon}}</span>
        </li>
      </ul>
    </demo-block>

    <demo-block :title="t('filled')" :card="true">
      <ul class="fs-row">
        <li
          class="fs-col fs-col-6"
          v-for="icon in config.filled"
          :key="icon"
          @click="copy(icon)"
        >
          <gw-icon :name="icon" />
          <span>{{icon}}</span>
        </li>
      </ul>
    </demo-block>
  </section>
</template>
<script>
import { ref } from 'vue'
import { Icon } from 'gw-vue-ui'
import { config } from '../src/assets/config'

// from https://30secondsofcode.org
function copyToClipboard (str) {
  const el = document.createElement('textarea')
  el.value = str
  el.setAttribute('readonly', '')
  el.style.position = 'absolute'
  el.style.left = '-9999px'
  document.body.appendChild(el)

  const selected =
    document.getSelection().rangeCount > 0
      ? document.getSelection().getRangeAt(0)
      : false

  el.select()
  document.execCommand('copy')
  document.body.removeChild(el)

  if (selected) {
    document.getSelection().removeAllRanges()
    document.getSelection().addRange(selected)
  }
}

const i18n = {
  title: '图标列表',
  badge: '徽标提示',
  basic: '基础图标',
  copied: '复制成功',
  outline: '线框风格',
  filled: '实底风格',
  demo: '用法示例',
  color: '图标颜色',
  size: '图标大小'
}

export default {
  components: {
    GwIcon: Icon
  },
  setup () {
    const t = (t) => { return i18n[t] }

    const copy = (iconName, options = {}) => {
      let tag = `<gw-icon name="${iconName}"`
      if ('color' in options) {
        tag = `${tag} color="${options.color}"`
      }
      if ('size' in options) {
        tag = `${tag} size="${options.size}"`
      }
      if ('dot' in options) {
        tag = `${tag} dot`
      }
      if ('badge' in options) {
        tag = `${tag} badge="${options.badge}"`
      }
      tag = `${tag} />`

      // 复制到剪切板
      copyToClipboard(tag)
      alert('复制成功！')
      console.log(`复制成功！：${tag}`)
    }
    return {
      t,
      config,
      copy,
      demoIcon: 'info-o',
      demoImage: 'https://b.yzcdn.cn/vant/icon-demo-1126.png'
    }
  }
}
</script>
